import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import LoadingContent from "../LoadingContent";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type LoadingContentStoryProps = {
    mode: string,
    brand: BrandType
    absolute: boolean
}

const LoadingContentStory = ({
    mode, brand, absolute
}: LoadingContentStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="LoadingContent">
                <Typography variant="h3">LoadingContent</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <LoadingContent
                        classes={classes}
                        absolute={absolute}
                        className=""
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        absolute: false
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        absolute: { type: "boolean" },
    }
}

storiesOf("Components/Loadings/LoadingContent", module)
    .add("Default", (args) => <LoadingContentStory {...args}/>, argsControls);
